import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'formatValue',
  standalone: true
})
export class FormatValuePipe implements PipeTransform {

  transform(value: any, ...args: any[]): any {
    if (value === null || value === undefined) {
      return ''; // K t c bit (gch ngang)
    }

    if (typeof value === 'number') {
      return new Intl.NumberFormat().format(value); // S dng NumberFormat  nh dng s
    }

    return value; // Tr v gi tr khng thay i nu khng phi s hoc null/undefined
  }
}
